<template>
  <div id="app">
    <v-app>
      <v-toolbar>
        <v-toolbar-side-icon></v-toolbar-side-icon>
        <v-toolbar-title>Title</v-toolbar-title>
      </v-toolbar>

      <v-content class="content">
        <router-view :coordinates="coordinates"/>
      </v-content>
    </v-app>
  </div>
</template>

<style scoped>
.content {
  margin-top: 15px;
}
</style>
<script>
export default {
  name: "App",

  methods: {
    getLocation() {
      this.$getLocation({
        enableHighAccuracy: true,
        timeout: Infinity,
        maximumAge: 0
      }).then(coordinates => {
        this.coordinates = coordinates;
      });
    }
  },

  mounted() {
    this.getLocation();
  },

  data() {
    return {
      coordinates: "1"
    };
  }
};
</script>
